import { Component, OnInit } from '@angular/core';
import { DataService } from '../../services/data.service';

@Component({
  selector: 'app-weather',
  templateUrl: './weather.component.html',
  styleUrls: ['./weather.component.css']
})
export class WeatherComponent implements OnInit {

  appTitle:string;
  currentLocation:Coordinates;
  currentLocationLon:number;
  currentLocationLat:number;
  weather:Weather;
  placeName:string;
  weatherTemp:Temperature;
  btnWeatherDeg:string;

  constructor(private dataService:DataService) {


  }

  ngOnInit() {
    this.appTitle = "Find my Weather";
  }

  onClick(){

    if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition( (position) => {
          this.currentLocation = position.coords
          this.currentLocationLon = position.coords.longitude
          this.currentLocationLat = position.coords.latitude

              this.dataService.getWeather(this.currentLocationLon, this.currentLocationLat).subscribe((posts) => {
                this.weather = posts.weather[0];
                this.placeName = posts.name;
                this.weatherTemp = posts.main;
                this.weatherTemp.tempDeg = "Â°C";
                this.btnWeatherDeg = "F";
              });

        });
    } else {
        alert("Geolocation is not supported by this browser.")
    }


  }

  switchDeg(){
    let cur_tempDeg = this.weatherTemp.tempDeg

    if (cur_tempDeg == "Â°C"){
      this.weatherTemp.tempDeg = "F";
      this.celsiusFah(this.weatherTemp.temp);
      this.btnWeatherDeg = "Â°C";
    }
    else if (cur_tempDeg == "F"){
      this.weatherTemp.tempDeg = "Â°C";
      this.fahCelsius(this.weatherTemp.temp);
      this.btnWeatherDeg = "F";
    }

  }

  celsiusFah(thisTemp){
    this.weatherTemp.temp = Math.round((thisTemp * 1.8) + 32);
  }

  fahCelsius(thisTemp){
    this.weatherTemp.temp = Math.round((thisTemp - 32) * .5556);
  }

}

interface Weather {
  description: string;
  icon: string;
  temp: number;
}

interface Temperature {
  temp: number;
  tempDeg: string;
}



